@import "../../_globalColor";

/* Compact Social Media Component */
.social-media-div {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
  margin: 20px 0;
}

.icon-button {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 42px;
  height: 42px;
  background: linear-gradient(135deg, 
    rgba(255, 255, 255, 0.1) 0%, 
    rgba(255, 255, 255, 0.05) 100%);
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  text-decoration: none;
  transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  box-shadow: 
    0 2px 8px rgba(0, 0, 0, 0.1),
    inset 0 1px 0 rgba(255, 255, 255, 0.1);
}

.icon-button i {
  font-size: 18px;
  transition: all 0.3s ease;
  filter: drop-shadow(0 1px 2px rgba(0, 0, 0, 0.1));
}

.icon-button:hover {
  transform: translateY(-2px) scale(1.05);
  box-shadow: 
    0 6px 20px rgba(0, 0, 0, 0.15),
    0 2px 6px rgba(0, 0, 0, 0.1),
    inset 0 1px 0 rgba(255, 255, 255, 0.2);
}

.icon-button:hover i {
  transform: scale(1.1);
  filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.2));
}

/* Platform-specific styling */
.github i { color: #333; }
.github:hover { 
  background: linear-gradient(135deg, rgba(51, 51, 51, 0.1) 0%, rgba(0, 0, 0, 0.05) 100%);
  border-color: rgba(51, 51, 51, 0.3);
}
.github:hover i { color: #000; }

.linkedin i { color: #0077b5; }
.linkedin:hover { 
  background: linear-gradient(135deg, rgba(0, 119, 181, 0.1) 0%, rgba(0, 88, 133, 0.05) 100%);
  border-color: rgba(0, 119, 181, 0.3);
}
.linkedin:hover i { color: #005885; }

.email i { color: #ea4335; }
.email:hover { 
  background: linear-gradient(135deg, rgba(234, 67, 53, 0.1) 0%, rgba(211, 59, 44, 0.05) 100%);
  border-color: rgba(234, 67, 53, 0.3);
}
.email:hover i { color: #d33b2c; }

.gitlab i { color: #fc6d26; }
.gitlab:hover { 
  background: linear-gradient(135deg, rgba(252, 109, 38, 0.1) 0%, rgba(226, 67, 41, 0.05) 100%);
  border-color: rgba(252, 109, 38, 0.3);
}
.gitlab:hover i { color: #e24329; }

.facebook i { color: #1877f2; }
.facebook:hover { 
  background: linear-gradient(135deg, rgba(24, 119, 242, 0.1) 0%, rgba(22, 111, 229, 0.05) 100%);
  border-color: rgba(24, 119, 242, 0.3);
}
.facebook:hover i { color: #166fe5; }

.medium i { color: #00ab6c; }
.medium:hover { 
  background: linear-gradient(135deg, rgba(0, 171, 108, 0.1) 0%, rgba(0, 138, 86, 0.05) 100%);
  border-color: rgba(0, 171, 108, 0.3);
}
.medium:hover i { color: #008a56; }

.stackoverflow i { color: #f48024; }
.stackoverflow:hover { 
  background: linear-gradient(135deg, rgba(244, 128, 36, 0.1) 0%, rgba(214, 104, 17, 0.05) 100%);
  border-color: rgba(244, 128, 36, 0.3);
}
.stackoverflow:hover i { color: #d66811; }

.instagram i { 
  background: linear-gradient(45deg, #f09433 0%, #e6683c 25%, #dc2743 50%, #cc2366 75%, #bc1888 100%);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}
.instagram:hover { 
  background: linear-gradient(135deg, rgba(240, 148, 51, 0.1) 0%, rgba(188, 24, 136, 0.05) 100%);
  border-color: rgba(240, 148, 51, 0.3);
}

.twitter i { color: #1da1f2; }
.twitter:hover { 
  background: linear-gradient(135deg, rgba(29, 161, 242, 0.1) 0%, rgba(13, 139, 217, 0.05) 100%);
  border-color: rgba(29, 161, 242, 0.3);
}
.twitter:hover i { color: #0d8bd9; }

.kaggle i { color: #20beff; }
.kaggle:hover { 
  background: linear-gradient(135deg, rgba(32, 190, 255, 0.1) 0%, rgba(0, 168, 230, 0.05) 100%);
  border-color: rgba(32, 190, 255, 0.3);
}
.kaggle:hover i { color: #00a8e6; }

/* Dark Mode */
.dark-mode {
  .icon-button {
    background: linear-gradient(135deg, 
      rgba(30, 30, 30, 0.2) 0%, 
      rgba(20, 20, 20, 0.15) 100%);
    border-color: rgba(255, 255, 255, 0.1);
    box-shadow: 
      0 2px 8px rgba(0, 0, 0, 0.2),
      inset 0 1px 0 rgba(255, 255, 255, 0.05);
  }

  .icon-button:hover {
    box-shadow: 
      0 6px 20px rgba(0, 0, 0, 0.3),
      0 2px 6px rgba(0, 0, 0, 0.2),
      inset 0 1px 0 rgba(255, 255, 255, 0.1);
  }

  /* Fix icon colors for dark mode */
  .github i { color: #ffffff !important; }
  .github:hover { 
    background: linear-gradient(135deg, rgba(255, 255, 255, 0.1) 0%, rgba(200, 200, 200, 0.05) 100%);
    border-color: rgba(255, 255, 255, 0.3);
  }
  .github:hover i { color: #f0f0f0 !important; }

  .linkedin i { color: #60a5fa !important; }
  .email i { color: #f87171 !important; }
  .gitlab i { color: #fb923c !important; }
  .facebook i { color: #60a5fa !important; }
  .medium i { color: #34d399 !important; }
  .stackoverflow i { color: #fbbf24 !important; }
  .twitter i { color: #60a5fa !important; }
  .kaggle i { color: #38bdf8 !important; }
}

/* Responsive */
@media (max-width: 768px) {
  .social-media-div {
    gap: 10px;
    margin: 16px 0;
  }
  
  .icon-button {
    width: 38px;
    height: 38px;
  }
  
  .icon-button i {
    font-size: 16px;
  }
}

@media (max-width: 480px) {
  .social-media-div {
    gap: 8px;
    margin: 12px 0;
  }
  
  .icon-button {
    width: 36px;
    height: 36px;
  }
  
  .icon-button i {
    font-size: 15px;
  }
}
