.metrics-main-div {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 80px 0;
}

.metrics-heading {
  font-size: 3rem;
  font-weight: 700;
  color: #333;
  text-align: center;
  margin-bottom: 60px;
}

.dark-mode .metrics-heading {
  color: white;
}

.metrics-subtitle {
  font-size: 1.1rem;
  color: #666;
  text-align: center;
  margin-bottom: 50px;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  line-height: 1.6;
}

.dark-mode .metrics-subtitle {
  color: #b0b0b0;
}

.metrics-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 30px;
  max-width: 1200px;
  width: 100%;
  padding: 0 20px;
}

/* Responsive Design */
@media (max-width: 1024px) {
  .metrics-grid {
    grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
    gap: 25px;
  }
  
  .metrics-heading {
    font-size: 2.5rem;
    margin-bottom: 50px;
  }
}

@media (max-width: 768px) {
  .metrics-main-div {
    padding: 60px 0;
  }
  
  .metrics-grid {
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 20px;
    padding: 0 15px;
  }
  
  .metrics-heading {
    font-size: 2rem;
    margin-bottom: 40px;
  }
}

@media (max-width: 480px) {
  .metrics-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 15px;
  }
  
  .metrics-heading {
    font-size: 1.8rem;
  }
}
