@import "../../_globalColor";

.experience-card, .experience-card-dark {
  display: flex;
  flex-direction: row;
  width: 100%;
  border: 1px solid $lightBorder1;
  border-radius: 10px;
  margin-bottom: 2rem;
  box-shadow: $lightBoxShadowDark 0px 10px 30px -15px;
  transition: all 0.3s ease-in-out;

  &:hover {
    box-shadow: $lightBoxShadowDark 0px 20px 30px -10px;
  }
}

.experience-card {
  background-color: $lightBackground2;
}

.experience-card-dark {
  background-color: $darkBackground;
}

.experience-card-logo-div {
  flex: 0 0 120px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px 0 0 10px;
}

.experience-card-logo {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  object-fit: contain;
  background-color: #fff;
  padding: 5px;
  border: 1px solid $lightBorder1;
}

.experience-card-body-div {
  flex: 1;
  padding: 1.5rem;
}

.experience-card-header-div {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.experience-card-heading-left {
  flex: 1;
}

.experience-card-title {
  font-size: 22px;
  font-weight: 700;
  margin: 0;
}

.experience-card-company {
  font-size: 18px;
  font-weight: 600;
  margin: 0.5rem 0 0 0;
  a {
    text-decoration: none;
    color: inherit;
  }
}

.experience-card-heading-right {
  text-align: right;
}

.experience-card-duration,
.experience-card-location {
  font-size: 16px;
  margin: 0;
}

.experience-card-duration {
  font-weight: 600;
}

.experience-card-description {
  margin-top: 1rem;
  text-align: left;
}

.experience-card-desc-bullets {
  ul {
    padding-left: 20px;
  }
  li {
    margin-bottom: 0.5rem;
  }
}

.experience-card-technologies {
  margin-top: 1.5rem;
}

.experience-card-metrics-achievements-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1.5rem;
  margin-top: 1.5rem;
}

.experience-card-technologies,
.experience-card-metrics,
.experience-card-achievements {
  h4 {
    font-size: 18px;
    font-weight: 600;
    margin-bottom: 1rem;
  }
}

.tech-pills-container {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.tech-pill {
  background-color: $lightBackground3;
  color: $textColor;
  padding: 0.25rem 0.75rem;
  border-radius: 15px;
  font-size: 14px;
  font-weight: 500;
}

.experience-card-dark .tech-pill {
  background-color: $darkBackground2;
  color: $textColorDark;
}

.metric-item {
  display: flex;
  margin-bottom: 0.5rem;
}

.metric-label {
  font-weight: 600;
  margin-right: 0.5rem;
}

.experience-card-achievements {
  margin-top: 1.5rem;
  ul {
    padding-left: 0;
    list-style: none;
  }
}

.achievement-item {
  display: flex;
  align-items: center;
  margin-bottom: 0.75rem;
}

.achievement-icon {
  color: $skillsColor;
  font-size: 20px;
  margin-right: 10px;
}

.metrics-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  gap: 1rem;
}

.metric-card {
  background-color: $lightBackground3;
  border: 1px solid $lightBorder1;
  border-radius: 8px;
  padding: 1rem;
  display: flex;
  align-items: center;
  gap: 1rem;
  transition: all 0.3s ease-in-out;

  &:hover {
    transform: translateY(-5px);
    box-shadow: $lightBoxShadowDark 0px 10px 20px -10px;
  }
}

.experience-card-dark .metric-card {
  background-color: $darkBackground2;
}

.metric-icon {
  font-size: 28px;
  color: $skillsColor;
}

.metric-text {
  display: flex;
  flex-direction: column;
}

.metric-value {
  font-size: 20px;
  font-weight: 700;
}

.metric-label {
  font-size: 14px;
  color: $subTitle;
}

.dark-mode-text {
  color: $textColorDark !important;
}

.experience-card-content-wrapper {
  display: flex;
  flex-direction: row;
  width: 100%;
  gap: 20px; // Add some space between main content and sidebar
}

.experience-card-main-content {
  flex: 3; // Takes up 3/4 of the space
}

.experience-card-sidebar {
  flex: 1; // Takes up 1/4 of the space
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding-left: 20px;
  border-left: 1px solid #e0e0e0; // A subtle separator

  .dark-mode & {
    border-left: 1px solid $darkBackground2;
  }
}

.experience-card-highlights,
.experience-card-technologies {
  h4 {
    margin-bottom: 10px;
    font-size: 1rem;
    font-weight: 600;
  }
}

.metrics-container {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.experience-card-achievements {
  ul {
    padding-left: 20px; // Keep bullet indentation
    margin: 0;
  }
  li {
    font-size: 0.9rem;
    margin-bottom: 5px;
  }
}

.tech-pills-container {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

/* Media Queries for Responsiveness */
@media (max-width: 1200px) {
  .experience-card-content-wrapper {
    flex-direction: column;
  }

  .experience-card-sidebar {
    flex-direction: row; // Side-by-side on smaller screens
    border-left: none;
    border-top: 1px solid #e0e0e0;
    padding-left: 0;
    padding-top: 20px;
    margin-top: 20px;

    .dark-mode & {
      border-top: 1px solid $darkBackground2;
    }
  }

  .experience-card-highlights,
  .experience-card-technologies {
    flex: 1;
  }
}

@media (max-width: 768px) {
  .experience-card, .experience-card-dark {
    flex-direction: column;
  }

  .experience-card-logo-div {
    flex: 0 0 auto;
    border-radius: 10px 10px 0 0;
    padding: 1rem;
  }

  .experience-card-body-div {
    padding: 15px;
  }

  .experience-card-sidebar {
    flex-direction: column;
  }

  .experience-card-header-div {
    flex-direction: column;
    align-items: flex-start;
  }

  .experience-card-heading-right {
    text-align: left;
    margin-top: 1rem;
  }
}
