@import "../../_globalColor";

.certification-card {
  background: $lightBackground1;
  border-radius: 20px;
  box-shadow: 0 10px 30px $lightBoxShadow;
  transition: all 0.3s ease-in-out;
  border: 1px solid $lightBorder1;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  text-decoration: none;

  &:hover {
    transform: translateY(-8px);
    box-shadow: 0 15px 40px $lightBoxShadowDark;

    .cert-overlay {
      opacity: 1;
    }

    .cert-logo {
      transform: scale(1.05);
    }
  }
}

.dark-mode .certification-card {
  background: $darkBackground2 !important;
  border-color: rgba(255, 255, 255, 0.1) !important;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2) !important;

  &:hover {
    box-shadow: 0 15px 40px rgba(0, 0, 0, 0.3) !important;
  }
}

.cert-image-container {
  position: relative;
  height: 200px;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  background-color: #f5f7fa;
  border-bottom: 1px solid $lightBorder1;
}

.dark-mode .cert-image-container {
  background-color: #2d3748;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.cert-logo {
  max-height: 90px;
  width: auto;
  max-width: 180px;
  transition: transform 0.3s ease-in-out;
  filter: grayscale(50%);
}

.certification-card:hover .cert-logo {
  filter: grayscale(0%);
}

.cert-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
  border-radius: 20px 20px 0 0;
}

.cert-overlay-content {
  text-align: center;
  color: $lightBackground1;
}

.cert-overlay-title {
  font-size: 1.2rem;
  font-weight: 600;
  margin: 0;
}

.cert-overlay-icon {
  font-size: 1.5rem;
  margin-top: 0.5rem;
  display: block;
}

.cert-content {
  padding: 1.5rem;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  text-align: center;
}

.cert-title {
  font-size: 1.25rem;
  font-weight: 700;
  color: $titleColor;
  margin: 0 0 0.5rem 0;
}

.dark-mode .cert-title {
  color: $textColorDark;
}

.cert-subtitle {
  font-size: 1rem;
  color: $subTitle;
  line-height: 1.5;
  margin: 0;
  flex-grow: 1;
}

.dark-mode .cert-subtitle {
  color: #a0aec0;
}

// Responsive Design
@media (max-width: 768px) {
  .certification-card {
    margin-bottom: 1rem;
  }

  .cert-image-container {
    height: 140px;
  }

  .cert-logo {
    height: 80px;
    max-width: 120px;
  }

  .cert-content {
    padding: 1.5rem;
  }

  .cert-title {
    font-size: 1.2rem;
  }

  .cert-subtitle {
    font-size: 0.85rem;
  }

  .cert-button {
    padding: 0.6rem 1rem;
    font-size: 0.8rem;
  }

  .cert-badge {
    width: 35px;
    height: 35px;
    top: 0.75rem;
    right: 0.75rem;
  }

  .cert-provider-icon {
    font-size: 1rem;
  }
}

@media (max-width: 480px) {
  .cert-content {
    padding: 1.25rem;
  }

  .cert-title {
    font-size: 1.1rem;
  }

  .cert-button {
    width: 100%;
    justify-content: center;
  }
}

@media screen and (max-width: 480px) and (min-width: 300px) {
  .cert-body-title {
    font-size: 18px;
  }
}