@import "../../_globalColor";

.card-image {
  width: 100%;
  height: 100%;
  object-fit: contain;
  border-radius: 12px;
  transition: transform 0.3s ease;
}
.card-title {
  color: $titleColor;
  font-size: 22px;
  line-height: 24px;
  font-weight: 700;
  margin: 0 0 15px 0;
  min-height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.card-subtitle {
  color: $cardSubtitle;
  font-size: 17px;
  line-height: 1.5rem;
  flex-grow: 1;
  margin-bottom: 1rem;
  text-align: justify;
  -webkit-hyphens: auto;
  hyphens: auto;
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 6;
  line-clamp: 6;
}
.project-card {
  display: flex;
  flex-direction: column;
  background-color: $lightBackground2;
  padding: 1.5rem;
  border-radius: 16px;
  border: 1px solid $lightBorder1;
  height: 520px;
  width: 100%;
  max-width: 400px;
  justify-content: flex-start;
  align-items: center;
  box-sizing: border-box;
  margin-bottom: 1rem;
}
.project-card-light {
  box-shadow: $lightBoxShadowDark 0px 10px 30px -15px;
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

.project-card-dark {
  box-shadow: 0px 10px 30px -15px $darkBoxShadow;
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.25s ease;
}

.project-detail {
  text-align: center;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  min-height: 0;
}
.project-card-light:hover {
  box-shadow: $lightBoxShadowDark 0px 20px 30px -10px;
  transform: translateY(-5px);
}
.project-card-dark:hover {
  box-shadow: 0px 20px 30px -10px $darkBoxShadow;
  transform: translateY(-5px);
}

.project-card:hover .card-image {
  transform: scale(1.05);
}
.project-image {
  position: relative;
  width: 180px;
  height: 180px;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto 1rem auto;
  border-radius: 12px;
  background-color: #f8f9fa;
  flex-shrink: 0;
}

.projects-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 2.5rem;
  margin-top: 2rem;
  align-items: start;
  justify-items: center;
}

.project-card-footer {
  margin-top: auto;
  padding-top: 1rem;
}

.project-card-footer span.project-tag {
  background: $buttonColor;
  color: $lightBackground3;
  vertical-align: middle;
  align-items: center;
  border-radius: 4px;
  display: inline-flex;
  font-size: 0.75rem;
  height: 2em;
  justify-content: center;
  white-space: nowrap;
  line-height: 1.5;
  margin: 0 0.5rem 0.5rem 0;
  padding: 0 0.75em;
  cursor: pointer;
  transition: 0.2s ease-in;
}

@media (max-width: 768px) {
  .project-subtitle {
    font-size: 16px;
    text-align: center;
  }
  
  .projects-container {
    grid-template-columns: 1fr;
    gap: 2rem;
    margin-top: 1.5rem;
    justify-items: center;
  }
  
  .project-card {
    height: 480px;
    padding: 1rem;
    max-width: 100%;
  }
  
  .project-image {
    width: 150px;
    height: 150px;
  }
  
  .card-title {
    font-size: 20px;
    min-height: 40px;
  }
  
  .card-subtitle {
    font-size: 15px;
    -webkit-line-clamp: 5;
    line-clamp: 5;
  }
}

@media (max-width: 480px) {
  .project-card {
    height: 420px;
    padding: 1rem;
    max-width: 100%;
  }
  
  .project-image {
    width: 120px;
    height: 120px;
  }
  
  .card-title {
    font-size: 18px;
    min-height: 36px;
  }
  
  .card-subtitle {
    font-size: 14px;
    text-align: center;
    -webkit-hyphens: none;
    hyphens: none;
    -webkit-line-clamp: 4;
    line-clamp: 4;
  }
}
