@import "../../_globalColor";

#certifications {
  padding: 5rem 0;
  min-height: 100vh;
  background-color: $lightBackground1;
  position: relative;
  overflow: hidden;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><defs><pattern id="cert-pattern" x="0" y="0" width="50" height="50" patternUnits="userSpaceOnUse"><text x="25" y="30" text-anchor="middle" font-size="20" fill="%23ffffff" opacity="0.05"></text></pattern></defs><rect width="100%" height="100%" fill="url(%23cert-pattern)"/></svg>');
    pointer-events: none;
  }
}

.dark-mode #certifications {
  background-color: $darkBackground !important;
}

.certifications-container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 2rem;
  position: relative;
  z-index: 1;
}

.certifications-header {
  text-align: center;
  margin-bottom: 4rem;
}

.certifications-heading {
  font-size: 3.5rem;
  font-weight: 800;
  margin-bottom: 1rem;
  color: $titleColor;
  line-height: 1.2;
}

.dark-mode .certifications-heading {
  color: $textColorDark;
}

.certifications-subtitle {
  font-size: 1.3rem;
  color: $subTitle;
  max-width: 700px;
  margin: 0 auto;
  line-height: 1.6;
  font-weight: 500;
}

.dark-mode .certifications-subtitle {
  color: $textColorDark !important;
}

.certifications-cards-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 2.5rem;
  margin-top: 3rem;
}

// Responsive Design
@media (max-width: 1380px) {
  .certifications-heading {
    font-size: 3rem;
  }
  
  .certifications-cards-container {
    grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
    gap: 2rem;
  }
}

@media (max-width: 768px) {
  #certifications {
    padding: 3rem 0;
  }

  .certifications-container {
    padding: 0 1rem;
  }

  .certifications-heading {
    font-size: 2.5rem;
    text-align: center;
  }
  
  .certifications-subtitle {
    font-size: 1.1rem;
    text-align: center;
  }
  
  .certifications-cards-container {
    grid-template-columns: 1fr;
    gap: 1.5rem;
    margin-top: 2rem;
  }
}

@media (max-width: 480px) {
  .certifications-heading {
    font-size: 2rem;
  }
  
  .certifications-subtitle {
    font-size: 1rem;
  }
}