@import "../../_globalColor";

.footer-text {
  text-align: center;
  color: $subTitle !important;
}

.dark-mode {
  color: $textColorDark !important;
}

.footer-div {
  margin-top: 2rem;
  padding-bottom: 1rem;
}


.buy-me-coffee {
  text-align: center;
}

.buy-me-coffee button {
  background-color: #ffcc66;
  border: none;
  color: #fff;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  border-radius: 5px;
}

.buy-me-coffee button:hover {
  background-color: #ffa500;
}
