@import "../../_globalColor";

.skills-main-div {
  display: flex;
  width: 100%;
  overflow: hidden;
}

.skills-text-div {
  margin-left: 50px;
}

.skills-main-div > * {
  flex: 1;
  margin-bottom: 30px;
}

.skills-image-div > img {
  max-width: 100%;
  height: auto;
}

.skills-heading {
  font-size: 56px;
  font-weight: 400;
}

.subTitle {
  color: $subTitle;
}

.key-capabilities {
  margin-top: 2rem;
  h2 {
    font-size: 1.5rem;
    margin-bottom: 1rem;
    border-bottom: 2px solid $accentColor;
    padding-bottom: 0.5rem;
    display: inline-block;
  }
  ul {
    list-style-type: none;
    padding-left: 0;
    li {
      margin-bottom: 0.75rem;
      font-size: 1.1rem;
    }
  }
}

.skills-by-category {
  margin-top: 2rem;
  .skill-category {
    margin-bottom: 2rem;
    h3 {
      font-size: 1.3rem;
      margin-bottom: 1rem;
      color: $subTitle;
    }
  }
}

.dev-icons {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  padding-left: 0;
  li {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 10px;
    min-width: 80px;
    text-align: center;
    transition: transform 0.3s ease-in-out, color 0.3s ease-in-out;

    &:hover {
      transform: scale(1.1);
      i {
        color: var(--skill-color);
      }
    }

    p {
      margin-top: 0.5rem;
      font-size: 0.9rem;
    }
  }
}

/* Media Query */
@media (max-width: 1380px) {
  .skills-heading {
    font-size: 40px;
  }
}

@media (max-width: 768px) {
  .skills-heading {
    font-size: 30px;
    text-align: center;
  }
  .greeting-text-p {
    font-size: 16px;
    line-height: normal;
  }
  .skills-main-div {
    flex-direction: column;
  }
  .skills-text-div {
    margin-left: 0px;
    margin: 20px;
  }
  .skills-text {
    font-size: 16px;
  }
  .skills-text-subtitle {
    font-size: 16px;
    text-align: center;
  }
  .skills-image-div {
    order: 2;
  }
  .dev-icons {
    justify-content: center;
  }
}
