@import "../../_globalColor";

.education-card-link {
  text-decoration: none;
}

.education-card, .education-card-dark {
  display: flex;
  flex-direction: row;
  width: 100%;
  margin-bottom: 20px;
  border-radius: 10px;
  transition: all 0.3s ease-in-out;
  overflow: hidden;
}

.education-card {
  background-color: $lightBackground1;
  box-shadow: 0px 4px 20px $lightBoxShadow;
  &:hover {
    box-shadow: 0px 6px 25px $lightBoxShadowDark;
  }
}

.education-card-dark {
  background-color: $darkBackground2;
  box-shadow: 0px 4px 20px $darkBoxShadow;
  &:hover {
    box-shadow: 0px 6px 25px $darkBoxShadow2;
  }
}

.education-card-logo-div {
  width: 120px;
  min-width: 120px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  transition: background-color 0.4s ease-in-out;
}

.education-card-logo {
  max-width: 100%;
  max-height: 80px;
  object-fit: contain;
  background-color: white;
  border-radius: 50%;
  padding: 5px;
  border: 2px solid white;
}

.education-card-body-div {
  flex: 1;
  padding: 20px;
  display: flex;
  flex-direction: column;
}

.education-card-content-wrapper {
  display: flex;
  flex-direction: row;
  width: 100%;
  gap: 20px;
}

.education-card-main-content {
  flex: 3;
}

.education-card-sidebar {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding-left: 20px;
  border-left: 1px solid #e0e0e0;

  .dark-mode & {
    border-left: 1px solid $darkBackground2;
  }
}

.education-card-highlights {
  h4 {
    margin-bottom: 10px;
    font-size: 1rem;
    font-weight: 600;
  }
}

.education-card-header-div {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 15px;
}

.education-card-heading-left {
  h5.education-text-school {
    font-size: 1.2rem;
    font-weight: 700;
    margin: 0;
  }
  p.education-text-subHeader {
    font-size: 1rem;
    color: $subTitle;
    margin: 5px 0 0 0;
  }
}

.education-card-heading-right {
  text-align: right;
  p {
    margin: 0;
    font-size: 0.9rem;
    color: $subTitle;
  }
  .education-text-grade {
    font-style: italic;
    margin-top: 5px;
  }
}

.education-text-desc {
  font-size: 0.95rem;
  line-height: 1.6;
  margin-bottom: 15px;
}

.education-text-bullets {
  ul {
    list-style: none;
    padding: 0;
    margin: 0;
    li {
      margin-bottom: 8px;
      font-size: 0.9rem;
      display: flex;
      align-items: flex-start;
      &::before {
        content: "";
        margin-right: 10px;
        font-size: 1.2em;
        line-height: 1;
      }
    }
  }
}

/* Dark Mode */
.dark-mode {
  .education-text-school,
  .education-text-subHeader,
  .education-text-duration,
  .education-text-desc,
  .subTitle {
    color: $textColorDark;
  }
  .education-text-subHeader,
  .education-text-duration,
  .education-text-grade {
    color: #bdc1c6; // Lighter grey for dark mode
  }
}

/* Responsive */
@media (max-width: 1200px) {
  .education-card-content-wrapper {
    flex-direction: column;
  }

  .education-card-sidebar {
    border-left: none;
    border-top: 1px solid #e0e0e0;
    padding-left: 0;
    padding-top: 20px;
    margin-top: 20px;

    .dark-mode & {
      border-top: 1px solid $darkBackground2;
    }
  }
}

@media (max-width: 768px) {
  .education-card,
  .education-card-dark {
    flex-direction: column;
  }
  .education-card-logo-div {
    width: 100%;
    padding: 20px 0;
  }
  .education-card-header-div {
    flex-direction: column;
    align-items: flex-start;
  }
  .education-card-heading-right {
    text-align: left;
    margin-top: 10px;
  }
}
