.value-prop-main-div {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 80px 20px;
  max-width: 1200px;
  margin: 0 auto;
}

.value-prop-heading {
  font-size: 3rem;
  font-weight: 700;
  color: #333;
  text-align: center;
  margin-bottom: 20px;
}

.dark-mode .value-prop-heading {
  color: white;
}

.value-prop-subtitle {
  font-size: 1.1rem;
  color: #666;
  text-align: center;
  margin-bottom: 60px;
  max-width: 600px;
  line-height: 1.6;
}

.dark-mode .value-prop-subtitle {
  color: #b0b0b0;
}

.value-prop-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 30px;
  width: 100%;
}

.value-prop-card {
  background: white;
  border-radius: 16px;
  padding: 30px;
  text-align: center;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
  border: 1px solid #f0f0f0;
  min-height: 220px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}

.value-prop-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.15);
}

.value-prop-card.dark-mode {
  background: #1e1e1e;
  border: 1px solid #333;
  color: white;
}

.value-prop-icon {
  font-size: 3rem;
  margin-bottom: 20px;
  display: block;
}

.value-prop-card-title {
  font-size: 1.5rem;
  font-weight: 600;
  color: #333;
  margin-bottom: 15px;
  line-height: 1.3;
}

.dark-mode .value-prop-card-title {
  color: #e0e0e0;
}

.value-prop-card-description {
  font-size: 1rem;
  color: #666;
  line-height: 1.6;
  margin: 0;
  flex-grow: 1;
}

.dark-mode .value-prop-card-description {
  color: #b0b0b0;
}

/* Responsive Design */
@media (max-width: 1024px) {
  .value-prop-grid {
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 25px;
  }
  
  .value-prop-heading {
    font-size: 2.5rem;
  }
}

@media (max-width: 768px) {
  .value-prop-main-div {
    padding: 60px 15px;
  }
  
  .value-prop-grid {
    grid-template-columns: 1fr;
    gap: 20px;
  }
  
  .value-prop-heading {
    font-size: 2rem;
  }
  
  .value-prop-card {
    padding: 25px;
    min-height: 200px;
  }
  
  .value-prop-icon {
    font-size: 2.5rem;
  }
  
  .value-prop-card-title {
    font-size: 1.3rem;
  }
  
  .value-prop-card-description {
    font-size: 0.95rem;
  }
}

@media (max-width: 480px) {
  .value-prop-card {
    padding: 20px;
    min-height: 180px;
  }
  
  .value-prop-icon {
    font-size: 2rem;
    margin-bottom: 15px;
  }
  
  .value-prop-card-title {
    font-size: 1.2rem;
    margin-bottom: 12px;
  }
  
  .value-prop-card-description {
    font-size: 0.9rem;
  }
}
