.metrics-card {
  background: white;
  border-radius: 12px;
  padding: 30px 20px;
  text-align: center;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
  border: 1px solid #f0f0f0;
  min-height: 160px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.metrics-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.15);
}

.metrics-card.dark-mode {
  background: #1e1e1e;
  border: 1px solid #333;
  color: white;
}

.metrics-number {
  font-size: 3rem;
  font-weight: 700;
  color: #55198b;
  margin-bottom: 8px;
  line-height: 1;
}

.dark-mode .metrics-number {
  color: #bb86fc;
}

.metrics-label {
  font-size: 1.2rem;
  font-weight: 600;
  color: #333;
  margin-bottom: 8px;
}

.dark-mode .metrics-label {
  color: #e0e0e0;
}

.metrics-description {
  font-size: 0.9rem;
  color: #666;
  line-height: 1.4;
}

.dark-mode .metrics-description {
  color: #b0b0b0;
}

/* Responsive Design */
@media (max-width: 768px) {
  .metrics-card {
    padding: 20px 15px;
    min-height: 140px;
  }
  
  .metrics-number {
    font-size: 2.5rem;
  }
  
  .metrics-label {
    font-size: 1.1rem;
  }
  
  .metrics-description {
    font-size: 0.85rem;
  }
}
