.project-title {
  font-size: 52px;
  font-weight: 400;
  line-height: normal;
}

.startup-project-text img {
  max-width: 100%;
  height: auto;
}

.repo-cards-div-main {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 2rem 2rem;
  margin-top: 2rem;
  align-items: stretch;
  justify-content: center;
}

/* Media Query */
@media (max-width: 1380px) {
  .project-title {
    font-size: 40px;
  }
}
@media (max-width: 768px) {
  .project-title {
    font-size: 30px;
    text-align: center;
  }
  .repo-cards-div-main {
    grid-template-columns: 1fr;
    gap: 1.5rem 1.5rem;
  }
}

/* Large screen adjustments */
@media (min-width: 1200px) {
  .repo-cards-div-main {
    gap: 2.5rem 2.5rem;
  }
}
